.page-main {
  margin: 0 auto;
  width: 320px;
  display: block;
  padding: 0;
  position: relative;


  &__wrapper {
    width: 320px;

    &--logo {
      position: absolute;
      top: -63px;
      background: url("../img/bg-back-mobile@1x.jpg") no-repeat $color_white;

      &::before {
        content: "";
        display: block;
        position: absolute;
        top: 176px;
        left: 0;
        width: 320px;
        height: 314px;
        background: url("../img/iphone-hand-mobile@1x.png") no-repeat;
      }
    }

    &--title {
      padding: 0;
      padding-top: 101px;
      margin: 0;
      position: absolute;
      top: -63px;
      min-height: 136px;
      background: url("../img/bg-back-mobile@1x.jpg") no-repeat $color_white;
      display: flex;
      flex-direction: column;
      border: 1px solid coral;
    }

    &--description {
      padding: 0;
      padding-top: 30px;
      padding-bottom: 30px;
      margin: 0;
      display: flex;
      flex-direction: column;
    }
  }

  &__title {
    padding: 0;
    margin: 0;

    @include font(24px, 300, $color_white,  1);
    text-align: center;
  }

  &__description {
    padding: 0;
    margin: 0;

    @include font(14px, 300, $color_gray,  2.14);
    text-align: center;
  }
}

@media (min-width: $tablet-width) {
  .page-main {
    width: $tablet-width;
    //height: auto;

    &__wrapper {
      width: $tablet-width;

      &--logo {
        padding-top: 184px;
        top: -111px;
        background: url("../img/bg-back-tablet@1x.jpg") no-repeat $color_white;
        //z-index: -2;

        &::before {
          top: 184px;
          left: 0;
          width: $tablet-width;
          height: 545px;
          background: url("../img/iphone-hand-tablet@1x.png") no-repeat;
          //z-index: -1;
        }
      }

      &--title {
        top: -111px;
        min-height: 330px;
        background: url("../img/bg-back-tablet@1x.jpg") no-repeat $color_white;
      }

      &--description {
        padding-top: 90px;
        padding-bottom: 80px;
      }
    }

    &__title {
      @include font(50px, 300, $color_white,  1);
    }

    &__description {
      @include font(18px, normal, $color_gray,  1.67);
    }
  }
}

@media (min-width: $desktop-width) {
  .page-main {
    width: $desktop-width;
    border: 1px solid yellowgreen;

    &__wrapper {
      width: $desktop-width;

      &--logo {
        //padding-top: 184px;
        //top: -111px;
        background: url("../img/bg-back-desktop@1x.jpg") no-repeat $color_white;
        z-index: -2;

        &::before {
          top: 208px;
          //left: 0;
          width: $desktop-width;
          height: 853px;
          background: url("../img/iphone-hand-desktop@1x.png") no-repeat;
          //z-index: -1;
        }
      }
    }
  }
}
